<template>
    <div class="login">
        <div>
            <img src="../assets/bizhi3.jpg" width="20%">
        </div>
    <el-form ref='AccountFrom' 
    :model='account' 
    :rules='rules'  label-width="0px"
    label-position="left" class="demo1-ruleForm"
    > 
    <h3 class="title">注册页面</h3>
    <el-form-item prop="username">
        <el-input type="text" 
            v-model="account.username" 
            maxlength="11"
            placeholder="请输入手机号"
        />
    </el-form-item>
    <el-form-item prop="oldpwd">
        <el-input type="password" 
            v-model="account.oldpwd" 
            placeholder="请输入密码"
        />
    </el-form-item>
    <el-form-item prop="pwd" :error="isExists">
        <el-input type="password" 
            v-model="account.pwd" 
            placeholder="请再次输入密码"
            @blur="checkPwd"
            @focus="getpwd"
        />
    </el-form-item>
    <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent='handleLogin' :loading="logining">注册</el-button>
    </el-form-item>
    </el-form>
    </div>
</template>

<script>
export default {
    name:'register',
    data(){
        return{
            logining: false,
            account: {
                username: '',
                oldpwd: '',
                pwd:''
            },
            rules: {
                username: [{
                    required: true,
                    message: '请输入账号',
                    trigger: 'blur'
                }],
                oldpwd: [{
                    required: true,
                    message: '请输入密码',
                    trigger: 'blur'
                }],
                pwd: [{
                    required: true,
                    message: '请输入密码',
                    trigger: 'blur'
                }]
            },
            checked: true,
            isExists:''
        }
    },
    methods: {
        getpwd(){
            this.isExists='';
        },
        checkPwd(){
            if(this.account.oldpwd!=this.account.pwd){
                this.isExists = "两次密码输入不一致"
            }
        },
        handleLogin(){
            this.$refs.AccountFrom.validate((valid)=>{
                if(valid){
                    //验证通过 可以提交
                    this.logining = true;
                    //将提交的数据封装
                    var loginParams = {phone: this.account.username, password: this.account.pwd}
                    this.axios.post("user/reg",this.$qs.stringify(loginParams)).then(res=>{
                        console.log(res)
                        if(res.data.code===200){
                            this.$message({
                                message: "注册成功",
                                type:'success'
                            });
                             this.$router.push("login");
                        }else{
                            this.logining = false;
                            this.$message({
                                message: res.data.message,
                                type:'error'
                            });
                        }
                    })
                }else{
                    console.log('error submit');
                    return false;
                }
            });
        }
    }
}
</script>
<style>
    body{
        background:#DFE9FB;
    }
    .demo1-ruleForm{
         width: 25%;
         margin-left: 37%;
    }
 .login {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.reg{
    padding-left: 150px;
    cursor: pointer;
}
</style>


